import React, { Component } from 'react';
import { Icon } from 'antd';
import {withRouter} from 'react-router-dom';
import {calcTotalPage, showPrevOrNext, getDevice} from '../../utils/common'
import AnchorScroll from '../anchorScroll'
import { connect } from 'react-redux'
import './index.css'
import {anchorsDataConfig} from '../../utils/anchorsConfig'
let anchorsData = anchorsDataConfig.slice(0)
class Index extends Component {
    constructor (props) {
        super(props)
        const pageSize = getDevice() === 'mobile' ? 6 : 9
        this.state = {
            current: 1,
            pageSize,
            total: calcTotalPage(anchorsData.length, pageSize)
        }
    }
    componentWillMount = () => {
        let {view} = this.props
        let {pageSize} = this.state
        anchorsData = anchorsData.map((item, index) => {
            item.active = false
            if (item.name === view) {
                item.active = true
                this.setState({
                    current: parseInt(index / pageSize, 10) + 1
                })
            }
            return item
        })
    }
    handleChangeAnchor = (type) => {
        let {current} = this.state
        if (type === 'next') {
            this.setState({
                current: current + 1
            })
        } else {
            this.setState({
                current: current - 1
            })
        }
    }
    handleChangeTopic = ({name}) => {
        let {history, view} = this.props
        if (view !== name) {
            switch (name) {
                case 'basic-javascript':
                  history.push('/basic-javascript')
                  break
                case 'web-security':
                  history.push('/web-security')
                  break
                case 'javascript-design-pattern':
                  history.push('/javascript-design-pattern')
                  break
                case 'es6':
                  history.push('/es6')
                  break
                case 'es7_8':
                  history.push('/es7_8')
                  break
                case 'drawcharts':
                  history.push('/drawcharts')
                  break
                case 'elementui':
                  history.push('/elementui')
                  break
                case 'vue':
                  history.push('/vue')
                  break
                case 'css':
                  history.push('/css')
                  break
                case 'babel':
                  history.push('/babel')
                  break
                case 'handbyhand':
                  history.push('/handbyhand')
                  break
                case 'react':
                  history.push('/react')
                  break
                case 'es9':
                  history.push('/es9')
                  break
                case 'http1_1':
                  history.push('/http1_1')
                  break
                case 'v8':
                  history.push('/v8')
                  break
                case 'chrome':
                  history.push('/chrome')
                  break
                default:
                  history.push('/')
                  break
            }
        }
        this.props.dispatch({
            type: 'update',
            view: name
        })
        anchorsData = anchorsData.map((item) => {
          if (name === item.name) {
            item.active = true
          } else {
            item.active = false
          }
          return item
        })
    }
    render () {
        let {current, pageSize, total} = this.state
        const anchorArrowClass = showPrevOrNext(current, total)
        const anchors = anchorsData.slice((current - 1) * pageSize, current * pageSize)
        console.log(anchors)
        return (<div className="footer">
            {(anchorArrowClass === 'prev' | anchorArrowClass === 'both') ? (<div className="anchor-prev center" onClick={this.handleChangeAnchor.bind(this, 'prev')}>
                <Icon type="arrow-left" />
            </div>) : null}
            <div className="anchorWrapper">
                <AnchorScroll anchors={anchors} handleChangeTopic={this.handleChangeTopic}/>
            </div>
            {(anchorArrowClass === 'next' | anchorArrowClass === 'both') ? (<div className="anchor-next center" onClick={this.handleChangeAnchor.bind(this, 'next')}>
                <Icon type="arrow-right" />
            </div>) : null}
        </div>)
    }
}
const mapStateToProps = state => {
    return {
        view: state.view
    }
}
export default withRouter(connect(mapStateToProps)(Index))
// export default withRouter(Index)


