import React, { Component } from 'react';
import {Icon} from 'antd'
// import logo from './logo.svg';
import './App.css';
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import {showGray} from './utils/specialdates'
import BasicJavascript from './views/basicjavascript/index'
import WebSecurity from './views/web-security/index'
import JavascriptDesignPattern from './views/javascript-design-pattern/index'
import ES6 from './views/es6'
import ES9 from './views/es9'
import ES7_8 from './views/es7_8'
import HTTP1_1 from './views/http1_1'
import BabelCom from './views/babel'
import Elementui from './views/elementui'
import DrawCharts from './views/drawcharts'
import VueCom from './views/vue'
import HandByHand from './views/handbyhand'
import ReactCom from './views/react'
import CSSCom from './views/css'
import V8Com from './views/v8'
import ChromeCom from './views/chrome'
import { connect } from 'react-redux'
import Footer from './components/footer'
class App extends Component {
  handleChangePage = (type) => {
    let {current, dispatch} = this.props
    if (type === 'prev') {
      dispatch({
        type: 'update',
        payload: {
          current: current - 1
        }
      })
    } else {
      dispatch({
        type: 'update',
        payload: {
          current: current + 1
        }
      })
    }
  }
  render() {
    let {appClass, location={}} = this.props
    let grayClass = showGray() ? 'gray' : ''
    return (
      <Router>
        <div className={'App ' + grayClass}>
          {(appClass === 'prev' || appClass === 'both') ? <div className="container-prev" onClick={this.handleChangePage.bind(this, 'prev')}><Icon type="arrow-left" /></div> : null}
          <div className="container"> 
            <div className="content">
                  <Switch>
                    <Route exact path="/react" component={ReactCom}/>
                    <Route exact path="/basic-javascript" component={BasicJavascript}/>
                    <Route exact path="/web-security" component={WebSecurity}/>
                    <Route exact path="/javascript-design-pattern" component={JavascriptDesignPattern}/>
                    <Route exact path="/es6" component={ES6}/>
                    <Route exact path="/es7_8" component={ES7_8}/>
                    <Route exact path="/es9" component={ES9}/>
                    <Route exact path="/vue" component={VueCom}/>
                    <Route exact path="/css" component={CSSCom}/>
                    <Route exact path="/handbyhand" component={HandByHand}/>
                    <Route exact path="/http1_1" component={HTTP1_1}/>
                    <Route exact path="/elementui" component={Elementui}/>
                    <Route exact path="/drawcharts" component={DrawCharts}/>
                    <Route exact path="/babel" component={BabelCom}/>
                    <Route exact path="/v8" component={V8Com}/>
                    <Route exact path="/chrome" component={ChromeCom}/>
                    <Route path="/" component={ReactCom}/> 
                  </Switch>
            </div>
            <Footer/>
          </div>
          {(appClass === 'next' || appClass === 'both') ? <div className="container-next" onClick={this.handleChangePage.bind(this, 'next')}><Icon type="arrow-right" /></div> : null}
        </div>
      </Router>
    );
  }
}
const mapStateToProps = state => {
  return {
    appClass: state.appClass,
    current: state.current,
  }
}
export default connect(mapStateToProps)(App)



