import {articles} from './articles'
import {anchorsDataConfig} from './anchorsConfig'
export function getArticlesByType (type) {
  return articles.filter(({category}) => {
    return category === type
  })
}
export function calcTotalPage (total, pageSize = 9) {
  if (total === 0) {
    return 1
  }
  let temp = Number.parseInt(total / pageSize, 10)
  if ((total - temp * pageSize) === 0) {
    return temp
  }
  return temp  + 1
}
export function showPrevOrNext (current, total) {
  if (total > current && current > 1) {
    return 'both'
  } else if (total > current && current === 1) {
    return 'next'
  } else if (total === current && current > 1) {
    return 'prev'
  } else {
    return ''
  }
}
export function getAnimationType (view) {
  let index = anchorsDataConfig.findIndex((item, index) => {
    return item.name === view
  })
  index = index === -1 ? 0 : index;
  const animations = ['animation1', 'animation2', 'animation3', 'animation4', 'animation5', 'animation6', 'animation7']
  index = parseInt(index % animations.length)
  return animations[index]
}
export function getDevice () {
  const rootStyles = getComputedStyle(document.documentElement); 
  const device = rootStyles.getPropertyValue('--device').trim();
  return device
}
export function calcPageSize () { 
  const device = getDevice();
  return device === 'mobile' ? 10000 : 9
}


