export function prettyDate(time){
    var date = new Date((time || "").replace(/-/g,"/").replace(/[TZ]/g," ")),
        diff = (((new Date()).getTime() - date.getTime()) / 1000),
        day_diff = Math.floor(diff / 86400);
    var isSameYear = date.getFullYear() ===  (new Date()).getFullYear()       
    if ( isNaN(day_diff) || day_diff < 0 || day_diff >= 365 || !isSameYear )
        return time;
    let diff_month = (new Date()).getMonth() - date.getMonth()    
    return day_diff === 0 && 
            (diff < 60 && "刚刚") ||
            (diff < 120 && "1 分钟前") ||
            (diff < 3600 && Math.floor( diff / 60 ) + " 分钟前") ||
            (diff < 7200 && "1 小时前") ||
            (diff < 86400 && Math.floor( diff / 3600 ) + " 小时前") ||
            (day_diff === 1 && " 昨天") ||
            (day_diff < 7 && day_diff + " 天前") ||
            ((day_diff > 7 && day_diff < 14) && " 1 周前") ||
            ((day_diff >= 14 && day_diff < 21) && " 2 周前") ||
            ((day_diff >=21 && day_diff < 28) && " 3 周前") ||
            ((day_diff >=28 && day_diff < 31) && " 4 周前") ||
            ((day_diff >=31) && diff_month + ' 个月前') ||
            time;
            //day_diff < 31 && Math.ceil( day_diff / 7 ) + " weeks ago";
}


