import React from 'react'
import PropTypes from 'prop-types'
import { Rate, Avatar } from 'antd';
import './index.css'
import {prettyDate} from '../../utils/prettydate'
const Index = ({avatar='http://www.dulinrain.top/author.jpg', date='', rate=5}) => {
    return (<div className="articleFooter">
            <div className="articleAvatar">
                <Avatar className="articleAvatar" size="small" shape="circle" src={avatar} />
            </div>
            <div className="articleDate" title={date}>{prettyDate(date)}</div>
            <div className="articleRate">
                <Rate className="articleRate" disabled defaultValue={rate} />
            </div>
            
        </div>
    )
}
export default Index


