import {showPrevOrNext, getAnimationType, calcPageSize} from '../utils/common'
let initState = {
    current: 1,
    total: 1,
    pageSize: calcPageSize(),
    appClass: showPrevOrNext(1, 1),
    // animationClas: getAnimationType(),
    view: 'basic-javascript'
}
export function test (state = initState, {type, payload = {}}) {
    let newState = {}
    let {view} = state
    switch (type) {
        case 'reset':
        newState = {...initState}
        break
        case 'update':
        newState = {...state, ...payload}
        newState.appClass = showPrevOrNext(newState.current, newState.total)
        break
        default:
        newState = {...state} 
    }
    return newState
}


