import React from 'react'
import PropTypes from 'prop-types'
import './index.css'
const Index = ({summary, link}) => {
    function handleClick () {
        link = link.slice(1)
        window.open(`//dulinrain.github.io/${link}`)
    }
    return (<div className="articleContent"  onClick={handleClick}>
            <p>
            {summary}
            </p>
        </div>
    )
}
export default Index


