import React, { Component } from 'react';
import { Row, Col } from 'antd';
import './index.css'
import {getArticlesByType, calcTotalPage, getAnimationType, getDevice} from '../../utils/common'
import { connect } from 'react-redux'
import ArticleItem from '../../components/articleItem'
class Index extends Component {
    constructor (props) {
      super(props)
      this.state = {
        articles: []
      }
    }
    componentWillMount = () => {
      let {view} = this.props
      this.props.dispatch({
        type: 'reset'
      })
      let articles = getArticlesByType(view)
      let total = calcTotalPage(articles.length, this.props.pageSize)
      this.setState({
        articles,
        total
      })
      this.props.dispatch({
        type: 'update',
        payload: {
          total,
          view 
        }
      })
    }
    render () {
        let {articles} = this.state
        let {current, pageSize = 9, view} = this.props
        let animationClas = getAnimationType(view);
        let device = getDevice();
        articles = device === 'mobile' ? articles : articles.slice((current - 1) * pageSize, current * pageSize)  
        return (<div  className={"conntent-inner " + animationClas}>
            {articles.map((item, index) => {
                return <ArticleItem {...item} key={index}/>
            })}
        </div>)
    }
}
const mapStateToProps = state => {
  return {
    current: state.current,
    total: state.total,
    pageSize: state.pageSize
  }
}
export default connect(mapStateToProps)(Index)


