import React from 'react'
import PropTypes from 'prop-types'
import './index.css'
import { Tooltip} from 'antd';
const Index = ({item, handleChangeTopic}) => {
    return (
        <Tooltip placement="topLeft" title={item.name} autoAdjustOverflow overlayClassName="custom-tooltip" arrowPointAtCenter>
            <div onClick={handleChangeTopic.bind(null, item)} className={'anchor' + (item.active ? ' active ' : ' ') + item.name}></div>
        </Tooltip>
    )
}
export default Index


