export function getAnniversary (str) {
    return new Date().getFullYear() - new Date(str).getFullYear()
}
export const specialDate = [{
    date: '2008-05-12',
    comment: `今天是汶川地震${getAnniversary('2008-05-12')}周年`
}]
export function isSameDay (str) {
    let today = new Date();
    let other = new Date(str);
    return (today.getMonth() === other.getMonth()) && (today.getDay() === other.getDay())
}
export function showGray () {
    return specialDate.some(({date}) => {
        return isSameDay(date)
    })
}


